import React from 'react'
import { connect } from 'react-redux'
import { NavLink } from 'react-router-dom'
import TableCards from '../Cards/TableCards'
import NavForFilter from './NavForFilter/NavForFilter'
import { color, age, otherSize, sizeShoesForKid, elements, material } from '../../elements/litleElementsArrs'



class categoriFilter extends React.Component{
  state = {
    disabled: false,
    cards: '',
    categoriCards: '',
    filterPriceCards: [],
    isLoading: false,
    filterCards: '',
    sale: [],
    cardOldPrice: [],
    priceMin: [],
    priceMax: [],
    minPriceValue: '',
    maxPriceValue: ''
  }
  async componentDidMount(){
   const card = await fetch(`https://shop-6b1fc-default-rtdb.firebaseio.com/card1.json`)
   .then(response => response.json())
   .then(card => Object.values(card))
      if(this.props.location.pathname.replace(/[^a-zа-яё]/gi, '') === 'man' || this.props.location.pathname.replace(/[^a-zа-яё]/gi, '') === 'woman'){
         const disabled = false
         const  cards = card.filter(card => card.sex === `${this.props.location.pathname.replace(/[^a-zа-яё]/gi, '')}`)
         const filterCards = [] 
         const newFilterArr = filterCards.concat(cards)
    
         this.setState({
         disabled,
         cards,
         categoriCards: [...newFilterArr],
         isLoading: !this.state.isLoading,
         filterCards: [...newFilterArr]
         })   
      } else{
         const  cards = card.filter(card => card.category === `${this.props.location.pathname.replace(/[^a-zа-яё-]/gi, '')}`)
         const disabled = true
         const filterCards = [] 
         const newFilterArr = filterCards.concat(cards)
    
         this.setState({
         disabled,
         cards,
         categoriCards: [...newFilterArr],
         isLoading: !this.state.isLoading,
         filterCards: [...newFilterArr]
         }) 
      }
  }

 

  clickRadio = (e) => {
    //category
    if(e.target.checked){
      const card = this.state.filterCards.filter(card => card.category === e.target.value)
      const arr = this.state.categoriCards.concat(card)
      this.setState({
        categoriCards: [...arr], cards: [...arr]
      })
    } else if(!e.target.checked){
      const arr = this.state.categoriCards.filter(card => card.category !== e.target.value)
      this.setState({
        categoriCards: [...arr], cards: [...arr]
      })
    }

    // //status
    // if(e.target.value === 'inStock'){
    //   if(e.target.checked){
    //     const oldCards = this.state.cards
    //     this.setState({
    //       cards: [...this.state.cards.filter(card => card.inStock)], sale: [...oldCards]
    //     })
    //   }else if(!e.target.checked){
        
    //     this.setState({
    //       cards: [...this.state.sale]
    //     })
    //   }
      
    // }

    //sale
    if(e.target.value === 'sale'){
      if(e.target.checked){
        const oldCards = this.state.categoriCards
        this.setState({
          categoriCards: [...this.state.categoriCards.filter(card => card.oldPrice)], sale: [...oldCards], cards: [...this.state.categoriCards.filter(card => card.oldPrice)]
        })
      }else if(!e.target.checked){
        this.setState({
          categoriCards: [...this.state.sale], cards: [...this.state.sale]
        })
      }
    }
      
  }

  minPrice = (e) => {
    const minPriceValue = e.target.value 
    const cards = this.state.priceMax.length > 0 && this.state.maxPriceValue
                  ? this.state.priceMax.filter(card => card.price >= minPriceValue)
                  : this.state.categoriCards.filter(card => card.price >= minPriceValue)
  
    this.setState({
      minPriceValue, 
      priceMin: [...cards], 
      cards: [...cards]
    })
  }
  maxPrice = (e) => {
    const maxPriceValue = e.target.value 
    const cards = this.state.priceMin.length > 0 && this.state.minPriceValue
                  ? this.state.priceMin.filter(card => card.price <= maxPriceValue)
                  : this.state.categoriCards.filter(card => card.price <= maxPriceValue)
  
    this.setState({
      maxPriceValue, 
      priceMax: [...cards], 
      cards: [...cards]
    })
  }

  // функция обьединяет два масива с уникальными значениями
//    arrayUnique = (array) => {
//     let a = array.concat();
//     for(let i=0; i<a.length; ++i) {
//         for(let j=i+1; j<a.length; ++j) {
//             if(a[i] === a[j])
//                 a.splice(j--, 1);
//         }
//     }

//     return a;
// }

render(){
      const cards = this.state.isLoading ? (<TableCards CARDS={this.state.cards}/>) : <div>loading...</div>
          
          const inp = <div> 
                          {elements.map((elem, index) => {
                            return <><input key={index} type="checkbox" onClick={this.clickRadio} id="category" disabled={this.state.disabled} value={elem.value} defaultChecked /><span>{elem.name}</span><br/></>
                          })}
                      </div>
          const inp2 = <div>
                        <p><input type="checkbox" onClick={this.clickRadio} id="sale" value="sale" />Товар со скидкой</p>
                      </div>
          const price = <div>
                      <p className="price">От<input min="1" type="number" defaultValue="1" value={this.state.minPrice} onChange={this.minPrice} />до<input min="1" type="number" defaultValue="9999" value={this.state.maxPrice} onChange={this.maxPrice} /></p>
                    </div>                         
  return(
    <div style={{display: 'flex', position: 'relative', minHeight: '100vh'}} className="container">
        <NavForFilter
          
          title={'Категории'}
          categoryInput={inp}

          title2={'Программа лояльности'}
          categoryInput2={inp2}

          title3={'Цена'}
          categoryInput3={price}
        /> 
        <div style={{transition: '1s', marginLeft: 200}} id="blockCards">
          {cards}
        </div>
    </div>  
    
  )
}
}

function mapStateToProps(state){
  return{
    cards: state.create.cards
  }
}

export default connect(mapStateToProps)(categoriFilter)
