import React from 'react'
import {NavLink, withRouter} from 'react-router-dom'
import Style from './Cards.module.scss'
import Star from '../../img/Star.png'

const Card = (props) => {
  // const Price = {marginTop: 10}
  return(
      <div data-index={props.index} className={Style.Card} >     {/*style={{width: '18rem', marginLeft: 20}}*/}
      <div className={Style.Name}><h5>{props.cardTitle}</h5></div>
      <NavLink to={`/cards/${props.id}`}><img src={props.cardSrc} alt={'foto'}/></NavLink>
      <div className={Style.Title}>
        <div style={!props.cardOldPrice ? {marginTop: 27} : null} >
        <div className={Style.Price}>
         {props.cardOldPrice ? <span className={Style.oldPrice}>{props.cardOldPrice} грн</span> : null} 
         <span style={!props.cardOldPrice ? {color: '#000'} : null} className={Style.price}>{props.cardPrice} грн</span>
        </div>
        <i><img style={{width: 19, height: 18}} src={Star}></img></i>
        <span className={Style.Size}>{props.cardSize}</span>
        </div> 
      </div>
      </div>
  )
}


export default withRouter(Card) 