import React from 'react'
import Style from './NavForFilter.module.scss'


const NavForFilter = (props) => {

 const OpenNav = () => {
    document.getElementById('navLeft').style.display = 'block'
    document.getElementById('filter').style.display = 'none'
    document.getElementById('navLeft').style.left = 0
    document.getElementById('blockCards').style.marginLeft = '200px'
}
  const DeleteNav = () => {
    document.getElementById('navLeft').style.left = '-225px'
    document.getElementById('filter').style.display = 'block'
    document.getElementById('blockCards').style.marginLeft = 0

  }

  const toggleBlock = (e) => {
    const div = e.target.parentElement.nextElementSibling
    const triangle = e.target.parentElement.children[1]
  if(div.style.transform === 'scaleY(0)'){ 
      div.style.transform = 'scaleY(1)'
      div.style.height = '100%'
      triangle.style.transform = 'rotate(0deg)'
    }else{
      div.style.height = '0'
      div.style.transform = 'scaleY(0)'
      triangle.style.transform = 'rotate(90deg)'
    }
    console.log(e.target.parentElement.children[1]);
      
  }

  return(
    <>
      <div id="navLeft"  className={Style.Nav}>
            <div>
                <p><b onClick={toggleBlock}>{props.title}</b>{props.categoryInput ? <span className={Style.triangle}></span> : null}</p>
                <div className={Style.SubBlock}>{props.categoryInput}</div> 
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title2}</b>{props.categoryInput2 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput2}</div>
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title3}</b>{props.categoryInput3 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput3}</div> 
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title4}</b>{props.categoryInput4 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput4}</div>
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title5}</b>{props.categoryInput5 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput5}</div>
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title6}</b>{props.categoryInput6 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput6}</div>
            </div>
            <div>
              <p><b onClick={toggleBlock}>{props.title7}</b>{props.categoryInput7 ? <span className={Style.triangle}></span> : null}</p>
              <div>{props.categoryInput7}</div>
            </div>
          <span onClick={DeleteNav} className={Style.Cross} >&#10005;</span>   
      </div>
      <span id="filter" onClick={OpenNav} className={Style.DeleteNav} >&#9776;</span>
    </>
  )
}
export default NavForFilter