import React, { Component } from 'react'
import { connect } from 'react-redux'
import { color, age, otherSize, sizeShoesForKid, elements, material } from '../../elements/litleElementsArrs'
import { createCard } from '../../State/action/cardsActions'
import Style from './CreateCard.module.scss'


class CreateCard extends Component {
  state = {
    selectCategory: '',
    title: '',
    text: '',
    img1: '',
    img2: '',
    img3: '',
    like: false,
    oldPrice: '',
    price: '',
    inCart: false,
    size: [],
    sex: '',
    quantity: 1,
    id: Date.now(),
    inStock: true,
    material: '',
    topMaterial: '',            //Материал верха
    innerMaterial: '',          //Внутренний материал
    outsoleMaterial: '',        //Материал подошвы
    insoleMaterial: '',         //Материал стельки
    season: '',                  //Сезон
    color: [],
    age: [],                   //Цвет
    countryOfOrigin: '',       //Страна производства
    clasp: '',                   //Застежка
    category: ''
  }


  selectCategoryHandler = (e) => {
    let newSelect = e.target.value
    this.setState({
      selectCategory: newSelect,
      
    })
  }

  titleHandler = (e) => {
    let newTitle = e.target.value
    this.setState({
      title: newTitle,
      
    })
  }
  TextHandler = (e) => {
    let newText = e.target.value
    this.setState({
      text: newText
    })
  }
  imgHandler1 = (e) => {
    let newImg = e.target.value
    console.log(e.target.value);
    this.setState({
      img1: newImg
    })
  }
  imgHandler2 = (e) => {
    let newImg = e.target.value
    console.log(e.target.value);
    this.setState({
      img2: newImg
    })
  }
  imgHandler3 = (e) => {
    let newImg = e.target.value
    console.log(e.target.value);
    this.setState({
      img3: newImg
    })
  }
  priceHandler = (e) => {
    let newPrice = e.target.value
    this.setState({
      price: newPrice
    })
  }
  OldPriceHandler = (e) => {
    let newPrice = e.target.value
    this.setState({
      oldPrice: newPrice
    })
  }
  sizeHandler = (e) => {
    let newText = this.state.size.concat([e.target.value]) 
    this.setState({
      size: newText
    })
  }

  discriptionHandler0 = (e) => {
    let newText = e.target.value
    this.setState({
      material: newText, 
    })
  }

  discriptionHandler1 = (e) => {
    let newText = e.target.value
    this.setState({
      topMaterial: newText, 
    })
  }
  discriptionHandler2 = (e) => {
    let newText = e.target.value
    this.setState({
      innerMaterial: newText, 
    })
  }
  discriptionHandler3 = (e) => {
    let newText = e.target.value
    this.setState({
      outsoleMaterial: newText, 
    })
  }
  discriptionHandler4 = (e) => {
    let newText = e.target.value
    this.setState({
      insoleMaterial: newText, 
    })
  }
  discriptionHandler5 = (e) => {
    let newText = e.target.value
    this.setState({
      season: newText, 
    })
  }
  discriptionHandler6 = (e) => {
    let newText = this.state.color.concat([e.target.value]) 
    this.setState({
      color: newText
    })
  }
  discriptionHandler7 = (e) => {
    let newText = e.target.value
    this.setState({
      countryOfOrigin: newText, 
    })
  }
  discriptionHandler8 = (e) => {
    let newText = e.target.value
    this.setState({
      clasp: newText, 
    })
  }
  discriptionHandler9 = (e) => {
    let newText = this.state.age.concat([e.target.value]) 
    this.setState({
      age: newText
    })
  }

  categoryHandler = (e) => {
    let category = e.target.value
    this.setState({
      category
    })
  }

  sexHandler =(e) => {
    let sex = e.target.value
    this.setState({
      sex
    })
  }



  submitHandler = (e) => {
    console.log();
    e.preventDefault()
   
    const {title, text, img1, img2, img3, price, oldPrice, inCart, size, quantity, inStock, topMaterial, innerMaterial, outsoleMaterial, insoleMaterial, season, color, countryOfOrigin, clasp, id, category, sex, like} = this.state
    const newCard =  { title, text, img1, img2, img3, price, oldPrice, inCart, size,quantity, inStock, topMaterial, innerMaterial, outsoleMaterial, insoleMaterial, season, color, countryOfOrigin, clasp, id, category, sex, like} 
    // this.props.createCard(newCard)

    fetch('https://shop-6b1fc-default-rtdb.firebaseio.com/card1.json', {
          method: 'POST',
          body: JSON.stringify(newCard),
          headers: {'Content-Type': 'application/json'}
        }).then(response => {
          return response.json()
        })

    this.setState({
      title: '',
      text: '',
      img1: '',
      img2: '',
      img3: '',
      oldPrice: '',
      price: '',
      inCart: false,
      size: '',
      sex: '',
      quantity: 1,
      id: Date.now(),
      inStock: true,
      material: '',
      topMaterial: '',            //Материал верха
      innerMaterial: '',          //Внутренний материал
      outsoleMaterial: '',        //Материал подошвы
      insoleMaterial: '',         //Материал стельки
      season: '',                  //Сезон
      color: '',                   //Цвет
      countryOfOrigin: '',       //Страна производства
      clasp: '',                   //Застежка
      category: ''                  
    })
  }

  render(){
    const shoes = (
   <>
   <div >
            <label >Категория товара</label>
            <select value={this.state.category} onChange={this.categoryHandler}>
            <option></option>
            {
              elements
                .filter(elem => elem.category === 'shoes')
                .map((elem, index) => {
                 return <option key={index} value={elem.value}>{elem.name}</option>  
                })
            }
          </select>
    </div>
      
      
      <div>
      <label >Материал верха</label>
      <input type="text" value={this.state.topMaterial} onChange={this.discriptionHandler1}/>
      </div>
      <div>
      <label >Внутренний материал</label>
      <input type="text" value={this.state.innerMaterial} onChange={this.discriptionHandler2}/>
      </div> 
      <div>
      <label >Материал подошвы</label>
      <input type="text" value={this.state.outsoleMaterial} onChange={this.discriptionHandler3}/> 
      </div> 
      <div>
      <label >Застежка</label>
      <input type="text" value={this.state.clasp} onChange={this.discriptionHandler8}/>
      </div> 
      <div>
            <label >Доступные размеры</label>
            <div className={Style.colorBlock}>
              {sizeShoesForKid.map((size, index) =>  {
                return(
                  <div>
                  <label key={index}>{size}</label>
                  <input type="checkbox" value={size} onChange={this.sizeHandler}/>
                  </div> 
                )
              })}
            </div>
          </div>
   </>   
    )
    const clothes = (
      <>
        <div >
            <label >Категория товара</label>
            <select value={this.state.category} onChange={this.categoryHandler}>
            <option></option>
            {
              elements
                .filter(elem => elem.category === 'clothes')
                .map((elem, index) => {
                 return <option key={index} value={elem.value}>{elem.name}</option>  
                })
            }
          </select>
        </div>
        <div>
          <label>Материал</label>
          <select value={this.state.material} onChange={this.discriptionHandler0}>
            <option></option>
            {
              material.map((elem, index) => {
                 return <option key={index} value={elem.value}>{elem.name}</option>  
                })
            }
          </select>
        </div>
        <div>
          <label>Рукав</label>
          <select>
            <option></option>
            <option>Длинный</option>
            <option>Короткий</option>
          </select>
        </div>
        <div>
            <label >Доступные размеры</label>
            <div className={Style.colorBlock}>
              {otherSize.map((size, index) =>  {
                return(
                  <div>
                  <label key={index}>{size}</label>
                  <input type="checkbox" value={size} onChange={this.sizeHandler}/>
                  </div>
                )
              })}
            </div>
          </div>
      </>
    )
    const hats = (
      <>
          <div >
              <label >Категория товара</label>
              <select value={this.state.category} onChange={this.categoryHandler}>
              <option></option>
              {
                elements
                  .filter(elem => elem.category === 'hats')
                  .map((elem, index) => {
                  return <option key={index} value={elem.value}>{elem.name}</option>  
                  })
            }
              </select>
          </div>
          <div>
            <label>Материал</label>
            <select value={this.state.material} onChange={this.discriptionHandler0}>
              <option></option>
              {
              material.map((elem, index) => {
                 return <option key={index} value={elem.value}>{elem.name}</option>  
                })
            }
            </select>
          </div>
      </>
    )
    return(
      <div className="container">
        <form className={Style.Form}>
        <h1>Создание карточки товара</h1>
          <div>
            <label >Название карточки</label>
            <input type="text" value={this.state.title} onChange={this.titleHandler} />
          </div>
          <div >
            <label >Описание</label>
            <textarea type="text" value={this.state.text} onChange={this.TextHandler} />
          </div>
          <div >
            <label >Пол</label>
            <select value={this.state.sex} onChange={this.sexHandler}>
              <option></option>
              <option value="man">Мужская</option>
              <option value="woman">Женская</option>
              <option value="unisex">Унисекс</option>
            </select>
          </div>
          <div >
            <label >Категория</label>
            <select value={this.state.selectCategory} onChange={this.selectCategoryHandler}>
            <option></option>
            <option value="shoes">Обувь</option>
            <option value="clothes">Одежда</option>
            <option value="hats">Головные уборы</option>
          </select>
          </div>
          {this.state.selectCategory === 'shoes'
          ? shoes
          : null}
          {this.state.selectCategory === 'clothes'
          ? clothes
          : null}
          <div>
          {
          this.state.selectCategory === 'hats'
          ? hats
          : null
          }
            <label >Страна производства</label>
            <input type="text" value={this.state.countryOfOrigin} onChange={this.discriptionHandler7}/>
          </div>
          <div>
            <label >Сезон</label>
            <input type="text" value={this.state.season} onChange={this.discriptionHandler5}/>
          </div>
          <div>
            <label >Цвет</label>
            <div className={Style.colorBlock}>
              {color.map((color, index) =>  {
                return(
                  <div>
                  <label key={index} style={{color: `${color}`}}>{color}</label>
                  <input type="checkbox" value={color} onChange={this.discriptionHandler6}/>
                  </div>
                )
              })}
            </div>
          </div>  
          <div >
            <label >Цена</label>
            <input type="text" value={this.state.price} onChange={this.priceHandler} />
            <label >Старая цена</label>
            <input type="text" value={this.state.oldPrice} onChange={this.OldPriceHandler} />
          </div>
          <div >
            <label >Возраст</label>
            <div className={Style.colorBlock}>
              {age.map((age, index) =>  {
                return(
                  <div>
                  <label key={index}>{age}</label>
                  <input type="checkbox" value={age} onChange={this.discriptionHandler9}/>
                  </div>
                )
              })}
            </div>
          </div>
          <div >
            <label >Картинки</label>
            <input type="text" placeholder="изображение 1 (главное)" value={this.state.img1} onChange={this.imgHandler1}/>
            <input type="text" placeholder="изображение 2" value={this.state.img2} onChange={this.imgHandler2}/>
            <input type="text" placeholder="изображение 3" value={this.state.img3} onChange={this.imgHandler3}/>
          </div>
          <button type="submit" className={Style.btn} onClick={this.submitHandler}>Опубликовать</button>
        </form> 
      </div>
    )
  }
}
function mapStateToProps(state){
  return{
    cards: state.create.cards
  }
}
function mapDispatchToProps(dispatch) {
  return{
    createCard: (newCard) => dispatch(createCard(newCard))
  }
  
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateCard)
