import React from 'react'
import { connect } from 'react-redux'
import {sizeShoesForKid} from '../../../../elements/litleElementsArrs'
import TableCards from '../../../Cards/TableCards'
import NavForFilter from '../NavForFilter'


class tShirts extends React.Component {

  state = {
    cards: '',
    categoriCards: '',
    filterPriceCards: [],
    isLoading: false,
    filterCards: '',
    sale: [],
    cardOldPrice: [],
    priceMin: [],
    priceMax: [],
    minPriceValue: '',
    maxPriceValue: ''
  }
 async componentDidMount(){
   const card = await fetch(`https://shop-6b1fc-default-rtdb.firebaseio.com/card1.json`)
                        .then(response => response.json())
                        .then(card => Object.values(card))
    const  cards = card.filter(card => card.category === 't-shirts')
    const filterCards = [] 
    const newFilterArr = filterCards.concat(cards)

    this.setState({
      cards,
      categoriCards: [...newFilterArr],
      isLoading: !this.state.isLoading,
      filterCards: [...newFilterArr]
    })  
}

  clickRadio = (e) => {
    //category
    if(e.target.checked){
      const card = this.state.filterCards.filter(card => card.category === e.target.value)
      const arr = this.state.categoriCards.concat(card)
      console.log(arr);
      this.setState({
        categoriCards: [...arr], cards: [...arr]
      })
    } else if(!e.target.checked){
      const arr = this.state.categoriCards.filter(card => card.category !== e.target.value)
      this.setState({
        categoriCards: [...arr], cards: [...arr]
      })
    }


    //sale
    if(e.target.value === 'sale'){
      if(e.target.checked){
        const oldCards = this.state.categoriCards
        this.setState({
          categoriCards: [...this.state.categoriCards.filter(card => card.oldPrice)], sale: [...oldCards], cards: [...this.state.categoriCards.filter(card => card.oldPrice)]
        })
      }else if(!e.target.checked){
        this.setState({
          categoriCards: [...this.state.sale], cards: [...this.state.sale]
        })
      }
    }
      
  }

  minPrice = (e) => {
    const minPriceValue = e.target.value 
    const cards = this.state.priceMax.length > 0 && this.state.maxPriceValue
                  ? this.state.priceMax.filter(card => card.price >= minPriceValue)
                  : this.state.categoriCards.filter(card => card.price >= minPriceValue)
  
    this.setState({
      minPriceValue, 
      priceMin: [...cards], 
      cards: [...cards]
    })
  }
  maxPrice = (e) => {
    const maxPriceValue = e.target.value 
    const cards = this.state.priceMin.length > 0 && this.state.minPriceValue
                  ? this.state.priceMin.filter(card => card.price <= maxPriceValue)
                  : this.state.categoriCards.filter(card => card.price <= maxPriceValue)
  
    this.setState({
      maxPriceValue, 
      priceMax: [...cards], 
      cards: [...cards]
    })
  }


render(){
      const cards = this.state.isLoading ? (<TableCards CARDS={this.state.cards}/>) : <div>loading...</div>
          
          const inp1 = <>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="man"  />Мужские<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="woman"  />Женские<br/>
                       </>
           const inp2 = <>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />С принтом<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Однотонная<br/>
                        </> 
           const inp3 = <>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />V-образный<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Круглый<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Поло<br/>
                        </>                                
          const inp4 = <>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Top Brands<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Новая коллекция<br/>
                        <input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />Спорт<br/>
                        </> 
          const inp5 = <div className="size">
                        {sizeShoesForKid.map(elem => {
                        return <><input type="checkbox" onClick={this.clickRadio} id="category" value="t-shirts"  />{elem}<br/></> 
                        })}
                      </div>                            
          const inp6 = <>
                        <p><input type="checkbox" onClick={this.clickRadio} id="sale" value="sale" />Товар со скидкой</p>
                       </>
          const price = <>
                          <p className="price">От<input min="1" type="number" defaultValue="1" value={this.state.minPrice} onChange={this.minPrice} />до<input min="1" type="number" defaultValue="9999" value={this.state.maxPrice} onChange={this.maxPrice} /></p>
                        </>                         
  return(
    <div style={{display: 'flex', position: 'relative', minHeight: '100vh'}} className="container">
        <NavForFilter
          title={'Пол'}
          categoryInput={inp1}

          title2={'Рисунок'}
          categoryInput2={inp2}

          title3={'Вырез'}
          categoryInput3={inp3}

          title4={'Cегмент'}
          categoryInput4={inp4}

          title5={'Размер'}
          categoryInput5={inp5}

          title6={'Программа лояльности'}
          categoryInput6={inp6}

          title7={'Цена'}
          categoryInput7={price}
          
        /> 
        <div style={{transition: '1s', marginLeft: 200}} id="blockCards">
          {cards}
        </div>
    </div>  
    
  )
}
}

function mapStateToProps(state){
  return{
    cards: state.create.cards
  }
}


export default connect(mapStateToProps)(tShirts)