import React from 'react'
import { connect } from 'react-redux'
import {NavLink} from 'react-router-dom'
import cart from '../../img/cart.png'
import user from '../../img/user.png'
import Style from './NavBar.module.scss'
import LOGO from '../../img/logo.png'
import { elements } from '../../elements/litleElementsArrs'


const NavBar = (props) => {
  

    const cartItem = (
      <div style={{display: 'inline-block', cursor: 'pointer', background: 'red', width: 20,height: 20, borderRadius: '50%', textAlign: 'center', position: 'absolute', top: 5, left: 37}}>
        <span style={{ display: 'block', color: '#fff', fontSize: 12, margin: '3px 0 0 0', padding: 0}}>
            {props.inCart.length}
        </span>
      </div>
    )
    return(
    <> 
     <nav className={`${Style.Nav} container`}>
        <div className={Style.leftUl}>
          <p className={Style.LOGO}><NavLink to="/"><img src={LOGO} /></NavLink></p>
          <div className={Style.Search}>
                <input type="search" placeholder="Поиск"/>
                <button>Искать</button>
          </div>
        </div>
        <div className={Style.tel}><span>+380978800930</span></div>
         <ul className={Style.rightUl}>  
           <li><NavLink to="/cart"><img src={cart} />{props.inCart.length > 0 ? cartItem : null}</NavLink></li>
           <li className={Style.user}><NavLink to="/"><img src={user} /><span>Войти</span></NavLink></li>
         </ul>   
      </nav>
      <div className={Style.SubNav}>
        <div className="container">
        <ul >
          <li className={Style.liCatalog}>Каталог товаров
          <div className={Style.Wrapli}> 
              <ul className={Style.subBar}>
                  <li className={Style.subBarLi1}>Обувь
                    <ul className={Style.subSubBar}>
                    {elements
                      .filter(elem => elem.category === 'shoes')
                      .map((elem, index) => {
                        return <li key={index}><a href={`/${elem.link}`}>{elem.name}</a></li>
                      })}
                    </ul>
                  </li>
                  <li className={Style.subBarLi2}>Верхняя одежда
                    <ul className={Style.subSubBar2}>
                      {elements
                      .filter(elem => elem.category === 'clothes')
                      .map((elem, index) => {
                        return <li key={index}><a href={`/${elem.link}`}>{elem.name}</a></li>
                      })}
                      </ul>
                  </li>
                  <li className={Style.subBarLi3}>Нижнее белье
                    <ul className={Style.subSubBar3}>
                    {elements
                      .filter(elem => elem.category === 'Underwear')
                      .map((elem, index) => {
                        return <li key={index}><a href={`/${elem.link}`}>{elem.name}</a></li>
                      })}
                      </ul>
                  </li>
                </ul>
                </div>
          </li>
          <li><a href="/man">Одежда для мальчиков</a></li>
          <li><a href="/woman">Одежда для девочек</a></li>
          <li><NavLink to="/cards">Одежда для новорожденных</NavLink></li>
          <li className={Style.sale}><NavLink to="/cards">Распродажа</NavLink></li>
        </ul>
        {/* <ul >
          <li><NavLink to="/cards/man">Мужская одежда</NavLink></li>
          <li><NavLink to="/cards/woman">Женская одежда</NavLink></li>
          <li><NavLink style={{color: 'red'}} to="/cards/sale">Sale</NavLink></li>
          <li><NavLink to="/cards">Доставка</NavLink></li>
          <li><NavLink to="/cards">Условия по возврату</NavLink></li>
        </ul> */}
        </div>  
    </div>
  </>
    )
  }
  


function mapStateToProps(state){
  return{
    inCart: state.create.inCart,
    cards: state.create.cards
  }
}

export default connect(mapStateToProps)(NavBar)



 {/* <ul className="navbar-nav" style={{width: '100%'}}>
          <li className="nav-item">
          <NavLink className="navbar-brand" to="/">LOGO</NavLink>
            </li>
            <li className="nav-item ">
              <NavLink className="nav-link" to="/cards">Cards</NavLink>
            </li>
            <li className="nav-item " style={{position: 'relative'}}>
              <NavLink className="nav-link" style={{margin: '4px 0 0 2px', padding: 0}} to="/cart">
                <img src={cart} style={{width: 25, cursor: 'pointer'}}/>
                    {props.inCart.length > 0 ? cartItem : null}
              </NavLink> 
            </li>
            <li className="nav-item" style={{marginLeft: 'auto'}}>
              <NavLink className="nav-link" to="/">Создать карточку товара</NavLink>
            </li>
          </ul> */}