import { CARDS_FETCH_DATA_SUCCESS, CREATE_CARD, ADD_TO_CART, REMOVE_CART, QUANTITY_MINUS, QUINTITY_PLUS, REMOVE_ELEM_FROM_CART, ADD_DATA } from "./actionTypes";


export function createCard(post){
  return{
    type: CREATE_CARD,
    payload: post
  }
}

export function addToCart(post){
  return{
    type: ADD_TO_CART,
    payload: post
  }
}

export function removeCart(id){
  return{
    type: REMOVE_CART,
    payload: id
  }
}

export function quantityMinus(id){
  return{
    type: QUANTITY_MINUS,
    payload: id
  }
}

export function quantityPlus(id){
  return{
    type: QUINTITY_PLUS,
    payload: id
  }
}

export function removeElemFromCart(id){
  return{
    type: REMOVE_ELEM_FROM_CART,
    payload: id
  }
}

export function addState(data){
  return{
    type: ADD_DATA,
    payload: data
  }
}

export function cardsFetchDataSuccess(cards){
  return{
    type: CARDS_FETCH_DATA_SUCCESS,
    payload: cards
  }
}

export function cardsFetchData(url){

  return(dispatch) => {
    fetch(url)
      .then(response => {
        if(!response.ok){
          throw new Error(response.statusText)
        }
        return response
      })
      .then(response => response.json())
      .then(cards => Object.values(cards))
      .then(cards => dispatch(cardsFetchDataSuccess(cards)))
  }
}