import React, { Component } from 'react'
import { connect } from 'react-redux'
import { addToCart, cardsFetchData, removeCart} from '../../State/action/cardsActions'
import Style from './CardsInfo.module.scss'

class CardInfo extends Component {
 
  state = {
    card: '',
    img: '',
    size: ''
  }
 async componentDidMount(){
      await  fetch(`https://shop-6b1fc-default-rtdb.firebaseio.com/card1.json`)   
          .then(response => response.json())
          .then(res => Object.values(res).find(card => card.id === parseInt(this.props.location.pathname.match(/\d+/))) )
          .then(card => {
            const img = card.img1
            const size = card.size
            this.setState({
              card,
              img,
              size
            })
          })     
 }


 
  clickImg = (e) => {
    this.setState({
      img: e.target.src     
    })
  }

  clickToCartHandler = (e) => {                         
    e.preventDefault()
      const elem = this.state.card
      elem.inCart = !elem.inCart
      this.props.addToCart(elem)
  }

  render(){
    const have = {
      color: 'green',
      fontSize: 17,
      backgroundImage: "url(" + "https://upload.wikimedia.org/wikipedia/commons/thumb/5/50/Yes_Check_Circle.svg/2048px-Yes_Check_Circle.svg.png" + ")",
      backgroundPosition: 'left 2px',
      backgroundSize: '19px',
      backgroundRepeat: 'no-repeat',
      paddingLeft: 25
    }
    const doNotHave = { 
      color: '#666',
      fontSize: 17,
      fontWeight: 'bold',
      paddingLeft: 25
    }
    const index = parseInt(this.props.location.pathname.match(/\d+/))

  return(
  
    <div className={`container ${Style.CardInfo}`}>
      <div className={Style.Information}>
        <h5 style={{backgroundImage: 'url("https://image.flaticon.com/icons/png/512/709/709790.png")'}}>Доставка</h5>
        <p>Самовывоз из нашего магазина</p>
        <p>"BabyShark" пгт.Новотроицкое, ул. Ленина, 112</p>
        <p>Отправка новой почтой по всей територии Украины</p>
        <h5 style={{backgroundImage: 'url("https://image.flaticon.com/icons/png/512/664/664460.png")'}}>Оплата</h5>
        <p>Наличными (только для пгт. Новотроицкое)</p>
        <p>Наложенный платёж</p>
        <p>Оплата банковской картой</p>
        <h5 style={{backgroundImage: 'url("https://image.flaticon.com/icons/png/512/664/664473.png")'}}>Гарантия</h5>
        <p>обмен / возврат товара в течение 14 дней</p>

      </div>
      <h1>{this.state.card.title}</h1>
      {
        <div className={Style.BlockImg}>
          <img src={this.state.img} />
          <div className={Style.litleImg}> 
          <img key={index} onMouseMove={this.clickImg} alt={'foto'} src={this.state.card.img1} />
          <img key={index} onMouseMove={this.clickImg} alt={'foto'} src={this.state.card.img2} />
          <img key={index} onMouseMove={this.clickImg} alt={'foto'} src={this.state.card.img3} />
          </div> 
        </div>
      }
      
        

            <div className={Style.Discription}>
             {this.state.size ? <p>Доступные размеры:</p> : null} 
             {this.state.size 
            ? <ul className={Style.BlockSize}>
             {
               Object.values(this.state.size).map((elem, index) => {
               return  <li key={index} >{elem}</li>
               })
             }
           </ul>
           : null} 
           {this.state.card.color ? <p>{this.state.card.color.map(elem => <span className={Style.BlockColor} style={{backgroundColor: `${elem}`}}></span>)}</p> : null}
              { 
                this.state.card.inStock 
                ? <span style={have}>Eсть в наличии</span>
                : <span style={doNotHave}>Нет в наличии</span>
              }
              
              { 
                this.state.card.oldPrice ?
                <span className={Style.oldPrice} >{this.state.card.oldPrice} грн</span>
                : null
              }
                <div className={Style.BlockPrice}>
                  <span className={Style.spanPrice}>{this.state.card.price} грн</span>
                  <button 
                  id={this.state.card.id} 
                  onClick={this.clickToCartHandler} 
                  className={Style.Btn}
                  disabled={this.state.card.inCart ? 'disabled' : null}
                  >{
                    this.state.card.inCart
                  ? 'В корзине'
                  : 'Добавить в корзину' 
                  }
                  </button>
                </div>
              
              <div className={Style.blockDiscription}>
                
                {this.state.card.topMaterial ? <p>Материал верха: <span>{this.state.card.topMaterial}</span></p> : null}
                {this.state.card.innerMaterial ?  <p>Внутренний материал: <span>{this.state.card.innerMaterial}</span></p> : null}
                {this.state.card.outsoleMaterial ? <p>Материал подошвы: <span>{this.state.card.outsoleMaterial}</span></p> : null}
                {this.state.card.insoleMaterial ? <p>Материал стельки: <span>{this.state.card.insoleMaterial}</span></p> : null}
                {this.state.card.season ? <p>Сезон: <span>{this.state.card.season}</span></p> : null}
                {this.state.card.countryOfOrigin ? <p>Страна производства: <span>{this.state.card.countryOfOrigin}</span></p> : null}
                {this.state.card.clasp ? <p>Застежка: <span>{this.state.card.clasp}</span></p> : null}
              </div>
            </div>
         
    </div>
  )
  }
  
}

function mapStateToProps(state){
  return{
    cards: state.create.cards
  }
}

function mapDispatchToProps(dispatch) {
  return{
    addToCart: item => dispatch(addToCart(item)),
    removeCart: id => dispatch(removeCart(id)),
    cardsFetchData: url => dispatch(cardsFetchData(url))
  }
  
}

export default connect(mapStateToProps, mapDispatchToProps)(CardInfo)
