import React from 'react'
import Classes from './CategoryList.module.scss'
import sale from '../../../img/sale.png'
import tShirt from '../../../img/image1.png'
import sniccers from '../../../img/sniccers.png'
import jackets from '../../../img/jackets.png'


const startImg = 'https://cdn.pixabay.com/photo/2017/06/21/20/51/tshirt-2428521_960_720.jpg'


class CategoryList extends React.Component {

  state = {
    img: startImg
  }

  mouseOver = (e) => {
    const arrImg = [sale, tShirt, sniccers, jackets]
    const img = arrImg.find(elem => elem === e.target.id)
    this.setState({
      img
    })
  }
  

  render(){
    return(
      <div className={Classes.CategoryBlock}>
        <div>
          <ul>
            <li><p onMouseOver={this.mouseOver} id={sale}>Горячие предложения</p></li>
            <li><p onMouseOver={this.mouseOver} id={tShirt}>Футболки</p></li>
            <li><p onMouseOver={this.mouseOver} id={sniccers}>Кроссовки</p></li>
            <li><p onMouseOver={this.mouseOver} id={jackets}>Верхняя одежда</p></li>
            <li><p onMouseOver={this.mouseOver} id={sale}>Куртки</p></li>
            <li><p onMouseOver={this.mouseOver} id={sale}>Аксесуары</p></li>
          </ul>
        </div>
        <div style={{backgroundImage: `url(${this.state.img})`}}></div>
      </div>
    )
  } 
}

export default CategoryList