import React from 'react'
import { connect } from 'react-redux'
import { addState, addToCart, removeCart } from '../../State/action/cardsActions'
import Card from './Card'



class TableCards extends React.Component {


  render() {
    return(
      <div className="d-flex flex-wrap container" >
        {this.props.CARDS.map((card, index) => {
          return <Card 
            key={index}
            cardSrc={card.img1}
            cardTitle={card.title}
            cardPrice={card.price}
            cardOldPrice={card.oldPrice}
            id={card.id}
            cardSize={card.size}
            disabled={card.inCart ? 'disabled' : null}
            btnTitle={ 
            card.inCart
            ? 'В корзине'
            : 'Добавить в корзину' 
            }
          />
        })}
      </div>
    )
  }
  
  
}


function mapDispatchToProps(dispatch) {
  return{
    addToCart: (item) => dispatch(addToCart(item)),
    removeCart: id => dispatch(removeCart(id)),
    addState: data => dispatch(addState(data))
  }
  
}

export default connect(null, mapDispatchToProps)(TableCards)