import NavBar from "./COMPONENT/NavBar/NavBar";
import { Route, Redirect, Switch } from "react-router-dom";
import TableCards from "./COMPONENT/Cards/TableCards";
import CreateCard from "./COMPONENT/CreateCard/CreateCard";
import tShirts from './COMPONENT/Filter/NavForFilter/Category/t-shirts';
import Cart from "./COMPONENT/Cart/Cart";
import CardInfo from "./COMPONENT/Cards/CardsInfo";
import { Component } from "react";
import { connect } from "react-redux";
import { addState, cardsFetchData } from "./State/action/cardsActions";
import MainPage from "./COMPONENT/MainPage/MainPage";
import Footer from "./COMPONENT/Footer/Footer";
import categoriFilter from "./COMPONENT/Filter/categoriFilter";
import { elements } from "./elements/litleElementsArrs";
// import Woman from "./COMPONENT/Filter/Woman";



class App extends Component {
   
  render(){
    return (
      <div style={{position: 'relative', marginBottom: 100}}>
        <NavBar />
        <Switch>
          <Route path="/" exact component={MainPage} />
          <Route path="/createcard" exact component={CreateCard} />
          {elements.map((elem, index) => {
            return <Route key={index} path={`/${elem.link}`} exact component={categoriFilter}/>
          })}
          <Route path="/man" exact component={categoriFilter} />  
          <Route path="/woman" exact component={categoriFilter} />   
          <Route path="/cards/sale" exact render={() => <TableCards CARDS={this.props.cards.filter(item => item.oldPrice)}/>} />  
          <Route path="/cards" exact render={() => <TableCards CARDS={this.props.cards}/>} />
          <Route path="/cart" component={Cart} />
          <Route path="/cards/:id" exact component={CardInfo} />
          <Redirect to="/cards" />
        </Switch>
        {/* <Footer /> */}
      </div>
    )
  }
  componentDidMount(){
    this.props.cardsFetchData('https://shop-6b1fc-default-rtdb.firebaseio.com/card1.json')
   }
}
function mapStateToProps(state){
  return{
    cards: state.create.cards
  }
}
function mapDispatchToProps(dispatch) {
  return{
    addState: data => dispatch(addState(data)),
    cardsFetchData: url => dispatch(cardsFetchData(url)) ////
  }
  
}

export default connect(mapStateToProps, mapDispatchToProps)(App)
