import React, { Component } from 'react'
import { connect } from 'react-redux'
import { quantityMinus, quantityPlus, removeElemFromCart } from '../../State/action/cardsActions'

class Cart extends Component {

  quantityHandlerMinus = (e) => {
    const id = e.target.parentElement.parentElement.id
    this.props.quantityMinus(id)
  }
  quantityHandlerPlus = (e) => {
    const id = e.target.parentElement.parentElement.id
    this.props.quantityPlus(id)
  }
  removeElem = (e) => {
    const id = e.target.parentElement.id
    this.props.removeElemFromCart(id)
  }
  
  render(){
    let item = 'товаров'
    if(this.props.inCart.length === 0){
      item = 'товаров'
    } else if (this.props.inCart.length === 1){
      item = 'товар'
    }else if (this.props.inCart.length >= 5) {
      item = 'товаров'
    } else if (this.props.inCart.length === 21) {
      item = 'товар'
    } else if (this.props.inCart.length >= 2){
      item = 'товара'
    } 
    return(
      <div className="jumbotron jumbotron-fluid">
        <div className="container">
          <h1 className="display-4">{`У вас в корзине ${this.props.inCart.length} ${item}`}</h1>
            <div className="d-flex justify-content-between flex-wrap">
              <ul style={{width: 400, margin: '30px 0 0 0', padding: 0}}>
                {
                  this.props.inCart.map((elem, index) => {
                  return (
                        <li key={index} id={index} className="d-flex justify-content-between align-items-center" style={{minWidth: 500, width: 600, marginBottom: 5}}>
                          <div className="d-flex justify-content-around align-items-center" style={{border: '2px solid #333', minWidth: 400, borderRadius: 10}}>
                            <img src={elem.img1} style={{width: 50}}/>
                            <p style={{margin: 0}}>{elem.title}</p>
                            <span>{elem.price * elem.quantity} грн</span>
                          </div>  
                          <div className="d-flex justify-content-between" style={{minWidth: 80}}>
                            <button onClick={this.quantityHandlerMinus} className="btn btn-outline-secondary btn-sm">-</button>
                            <span>{elem.quantity}</span>
                            <button onClick={this.quantityHandlerPlus} className="btn btn-outline-secondary btn-sm">+</button>
                          </div>
                          <button onClick={this.removeElem} className="btn btn-outline-danger btn-sm" style={{height: 30}}>✖</button>
                        </li>
                  )
                  })
                }
              </ul>
              <div style={{margin: '10px 300px 0 0'}}>
                <h3 style={{minWidth: 200, marginLeft: 10}}>
                  Итого: <span>{
                  this.props.inCart.length > 0 ?
                  this.props.inCart.map(elem => elem.price * elem.quantity).reduce((x, y) => x + y)
                    : 0
                  } грн</span>
                </h3>
              </div>
            </div>
        </div>
      </div>
    )
  }
  
}

function mapStateToProps(state){
  return{
    inCart: state.create.inCart
  }
}
function mapDispatchToProps(dispatch) {
  return{
    quantityMinus: id => dispatch(quantityMinus(id)),
    quantityPlus: id => dispatch(quantityPlus(id)),
    removeElemFromCart: id => dispatch(removeElemFromCart(id))
  }
  
}
export default connect(mapStateToProps, mapDispatchToProps)(Cart)