export const createCard = (state = initialState, action) => {
  switch(action.type){
    case CARDS_FETCH_DATA_SUCCESS:
      return {
        ...state, cards: state.cards.concat(action.payload)
      }
    case CREATE_CARD:
      return {
        ...state, cards: state.cards.concat([action.payload]).reverse()
      }
    case ADD_TO_CART:
      return {
        ...state, cards: [...state.cards], inCart: state.inCart.concat([action.payload])
      }
    case REMOVE_CART: 
      state.cards[action.payload].inCart = !state.cards[action.payload].inCart
      state.inCart.map(item => {
        if(item.id === action.payload){
          state.inCart.splice(item, 1) 
        }
      })
      return {
        ...state, cards: [...state.cards], inCart: [...state.inCart]
      } 
    case QUANTITY_MINUS:
      state.inCart[action.payload].quantity > 1
      ? state.inCart[action.payload].quantity = state.inCart[action.payload].quantity - 1
      : state.inCart[action.payload].quantity = state.inCart[action.payload].quantity
      return{
        ...state, inCart: [...state.inCart]
      } 
    case QUINTITY_PLUS:
      state.inCart[action.payload].quantity = state.inCart[action.payload].quantity + 1
      return{
        ...state, inCart: [...state.inCart]
      }
    case REMOVE_ELEM_FROM_CART:
      state.inCart.splice(action.payload, 1)
      return{
        ...state, inCart: [...state.inCart]
      } 
    case ADD_DATA:
      return{
        ...state, cards: state.cards.concat(action.payload)
      }       
    default:
      return state
  }
}
